

import { defineComponent, ref } from 'vue';
import MovieList from './components/MovieList.vue'
import VueTailwindPagination from "@ocrv/vue-tailwind-pagination";
  import '@ocrv/vue-tailwind-pagination/dist/style.css'
import Movie from './types/Movie'
import OrderTerm from './types/OrderTerm'
import MovieService from './services/MovieService.js'

export default defineComponent({
  name: 'App',
  components: {
    MovieList,
    VueTailwindPagination
  },
  props: {
  },
  setup(){
    const movies = ref<Movie[]>([])
    const order = ref<OrderTerm>('Title')
    const searchTerm = ref<string>('')
    const favourite = ref<boolean>(false)
    const page = ref<number>(1)
    const totalPages = ref<number>(10)
    // const perPage = ref<number>(10)

    const handleClick = (term: OrderTerm) => {
      order.value = term
    }

    const showFavourites = ()=>{
      favourite.value = !favourite.value;
    }

    const searchApi = (e) => {
      handleSearch(searchTerm.value, page.value)
    }

    const handleSearch = ( name: string , page: number) => {
      favourite.value = false
      movies.value = [];
      MovieService.searchTitle(name, page)
      .then(res => {
        movies.value = [...res.data.data];
        page = res.data.page
        totalPages.value = res.data.total_pages
        searchTerm.value = ''
      })
      .catch(error => {
        console.log("There was an error", error.response)
      })
    }

    const getAll = () => {
      //initialize the values
      favourite.value = false
      movies.value = []

      MovieService.getPaginated(page)
      .then(res => {
        movies.value = [...res.data.data];
        searchTerm.value = ''
        page.value = res.data.page
      })
      .catch(error => {
        console.log("There was an error", error.response)
      })
    }

    const getpaginated = (event) => {
      movies.value = []
      MovieService.getPaginated(event)
      .then(res => {
        movies.value = [...res.data.data];
        searchTerm.value = ''
        page.value = res.data.page
      })
      .catch(error => {
        console.log("There was an error", error.response)
      })
    }

    return {
      movies,
      handleClick,
      favourite,
      searchApi,
      order,
      searchTerm,
      page,
      totalPages,
      getAll,
      getpaginated,
      showFavourites
    }
  },
  mounted(){
    MovieService.getMovies()
    .then(res => {
      console.log(res.data);
      this.movies = [...res.data.data];
      this.page = res.data.page
      this.totalPages = res.data.total_pages
      console.log("movei ", this.movies)
      console.log("page ", this.page)
      console.log("total page ", this.totalPages)
    })
    .catch(error => {
      console.log("There was an error", error.response)
    })
  }
});
